"use client"
import getStripe from "@/utils/stripe";
import PaymentCard from "@/components/order/PaymentCard";
import { useAppSelector } from "@/hooks/reduxHooks"
import { Order } from "@/redux/reduxTypes";
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import axios from "axios";
import { Lock, Ship } from "lucide-react";
import { useParams } from "next/navigation";
import { useEffect, useState } from "react";
import { convertToSubcurrency } from "@/utils/payment";

enum Shipping {
    Economy = 2.20,
    Standart = 3.20,
    Express = 5.20,
}

const Page = () => {

    const { user } = useAppSelector(state => state.auth);
    const { id } = useParams() as { id: string };
    const [order, setOrder] = useState<Order | null>(null);
    const [shipping, setShipping] = useState<Shipping>(Shipping.Economy);
    const getOrder = async () => {
        try {
            const res = await axios.get(`http://localhost:5200/api/order/get-order/${id}`, { withCredentials: true });
            setOrder(res.data);
        } catch (err) {
            console.error(err);
        }
    }

    useEffect(() => {
        getOrder();

    }, [])

    return (
        <div>Page</div>
    )
}

export default Page